.guarantee {
  // padding-top: 50px;
  //   &-wrapper {
  //     padding: 30px 0;
  //     background-color: #f1f1f1;
  //   }
  &-box {
    background-color: #f1f1f1;
    padding-top: 30px;
    padding-bottom: 31px;
  }

  &-title {
    @extend %title;
    margin-bottom: 30px;
    text-align: center;
  }

  &-item {
    @include font(500, 12px, 20px, 0.03em);
    color: #3d3d3d;

    display: inline-block;
    padding-left: 20px;

    position: relative;

    &-accent {
      color: $accent-color;
    }

    &:not(:last-child) {
      padding-bottom: 23px;
      border-left: 1px dashed $main-text-color;
    }

    &::before {
      content: '';
      width: 14px;
      height: 20px;

      position: absolute;
      top: 0;
      left: -7px;

      background-image: url('../images/header_footer/campfire.svg');

      background-size: contain;
      background-repeat: no-repeat;
    }

    &:hover::before,
    &:focus::before {
      animation: animate125 1000ms infinite 10ms;
      left: -11px;
    }
  }
}

@keyframes animate125 {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.25);
  }

  100% {
    transform: scale(1);
  }
}

@media screen and (min-width: 768px) {
  .guarantee {
    &-box {
      padding-top: 30px;
      padding-bottom: 30px;
    }

    &-item:not(:last-child) {
      padding-bottom: 23px;
    }

    &-list {
      padding-left: 78px;
      padding-right: 50px;
    }
  }
}

@media screen and (min-width: 1316px) {
  .guarantee {
    padding-top: 65px;

    &-box {
      padding-top: 50px;
      padding-bottom: 45px;

      //     &-wrapper {
      //       padding: 45px 0;
    }

    &-list {
      padding-left: 81px;
      padding-right: 81px;
    }

    &-title {
      margin-bottom: 50px;
    }

    &-item {
      @include font(500, 20px, 28px, 0.03em);
      padding-left: 30px;
      margin-right: 0;
      margin-left: 0;

      &::before {
        width: 22px;
        height: 30px;
        left: -10px;
      }
    }
  }
}
